export interface Env {
  USER_STATE: DurableObjectNamespace;
}

type WordleStats = {
  attempts?: number;
  lastWord?: string;
};

type GameStats = {
  gamesPlayed: number;
  gamesWon: number;
  totalTime: number;
  wordle?: WordleStats;
  sudoku?: Record<string, unknown>;
  quiz?: Record<string, unknown>;
};

type WordleSession = {
  gameId: string;
  word: string;
  startedAt: string;
  expiresAt: string;
  attempts: number;
  guessTimestamps: number[];
};

type UserRecord = {
  userId: string;
  name: string;
  coins: number;
  stats: GameStats;
  createdAt: string;
  updatedAt: string;
  wordleSession?: WordleSession;
  hintUses?: number;
  searchUses?: number;
};

const ALLOWED_ORIGINS = new Set([
  "http://localhost:5173",
  "http://localhost:8081",
]);

